import { useState, useEffect } from "react";
import { ModernTable } from "@/components/ModernTable";
import Header from "@/components/Header";
import Page from "@/components/Page";
import { getAllContactLists, getContactsForList, deleteContactList, restoreContactList } from "@/api/services/contact/contactService";
import { useNavigate } from "react-router-dom";
import {
  Edit,
  Eye,
  Trash,
  Plus,
  Users,
  Calendar,
  ListFilter,
  Mail,
  UserPlus,
  ListChecks,
  Share2,
  MoreHorizontal,
  RotateCcw,
  AlertCircle,
} from "lucide-react";
import { AddContactList } from "./AddContactList";
import { Badge } from "@/components/ui/badge";
import { useSearchParams } from "react-router-dom";
import ListGrid from "@/components/ListGrid";
import ListFilterPopover from "@/components/ListFilterPopover";
import CustomPagination from "@/components/CustomPagination";
import { usePermissions } from "@/hooks/usePermissions";
import { toast } from "sonner";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Sheet, SheetContent } from "@/components/ui/sheet";
import { Button } from "@/components/ui/button";
import axiosInstance from "@/api/axios/axiosInstance";
import { useAuth } from "@/context/AuthContext";

const Lists = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();
  const { isSuperAdmin } = usePermissions();
  const { user } = useAuth();
  const [debouncedSearch, setDebouncedSearch] = useState(
    searchParams.get("search") || ""
  );
  const [searchQuery, setSearchQuery] = useState(debouncedSearch);
  const [viewMode, setViewMode] = useState<"list" | "grid">(
    searchParams.get("view") === "grid" ? "grid" : "list"
  );
  const [activeFilters, setActiveFilters] = useState<any>({});
  const [showFilters, setShowFilters] = useState(false);
  const [lists, setLists] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [totalLists, setTotalLists] = useState(0);
  const [totalPages, setTotalPages] = useState(0);

  // Delete confirmation dialog state
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [listToDelete, setListToDelete] = useState<any>(null);

  // Restore confirmation dialog state
  const [restoreDialogOpen, setRestoreDialogOpen] = useState(false);
  const [listToRestore, setListToRestore] = useState<any>(null);

  // Insufficient credits modal state
  const [showInsufficientCreditsModal, setShowInsufficientCreditsModal] = useState(false);
  const [currentBalance, setCurrentBalance] = useState(0);

  // Add List sheet state
  const [showAddListSheet, setShowAddListSheet] = useState(false);

  // Extract current page from searchParams
  const currentPage = parseInt(searchParams.get("page") || "1");
  const itemsPerPage = 10;

  // Handle Add List button click with credit checking
  const handleAddListClick = async () => {
    // Super admins have unlimited access
    if (isSuperAdmin) {
      setShowAddListSheet(true);
      return;
    }

    try {
      // Check credit balance for regular users
      const response = await axiosInstance.get('/api/credits/balance');
      const balance = response.data.data;

      if (!balance || parseFloat(balance.available_credits) < 1) {
        // Store current balance and show insufficient credits modal
        setCurrentBalance(balance?.available_credits || 0);
        setShowInsufficientCreditsModal(true);
        return;
      }

      // User has sufficient credits, show the add list sheet
      setShowAddListSheet(true);
    } catch (error) {
      console.error('Error checking credit balance:', error);
      toast.error('Unable to verify credit balance. Please try again.');
    }
  };

  // Fetch lists from API
  const fetchLists = async () => {
    try {
      setLoading(true);
      // Super admins can see deleted lists
      const response = await getAllContactLists(currentPage, searchQuery, isSuperAdmin);
      if (response.success) {
        setLists(response.data);
        setTotalLists(response.pagination.totalLists);
        setTotalPages(response.pagination.totalPages);
      }
    } catch (error) {
      console.error("Error fetching lists:", error);
      setLists([]);
    } finally {
      setLoading(false);
    }
  };

  // Debouncing effect for search
  useEffect(() => {
    const handler = setTimeout(() => {
      setSearchQuery(debouncedSearch);
      setSearchParams((prev) => {
        const newParams = new URLSearchParams(prev);
        if (debouncedSearch) {
          newParams.set("search", debouncedSearch);
        } else {
          newParams.delete("search");
        }
        return newParams;
      });
    }, 500); // Reduced debounce time for better UX

    return () => clearTimeout(handler);
  }, [debouncedSearch, setSearchParams]);

  // Fetch lists when page, search, or super admin status changes
  useEffect(() => {
    fetchLists();
  }, [currentPage, searchQuery, isSuperAdmin]);

  // Update URL when view mode changes
  useEffect(() => {
    setSearchParams((prev) => {
      const newParams = new URLSearchParams(prev);
      if (viewMode === "grid") {
        newParams.set("view", "grid");
      } else {
        newParams.delete("view");
      }
      return newParams;
    });
  }, [viewMode, setSearchParams]);

  // Function to format date
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return new Intl.DateTimeFormat("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
    }).format(date);
  };

  // Handle list click to view details
  const handleListClick = (list: any) => {
    // Don't allow clicking on disabled lists unless super admin
    if (list.is_deleted && !isSuperAdmin) {
      toast.error("This list has been disabled and is not accessible.");
      return;
    }
    navigate(`/lists/${list.id}`);
  };

  const handleSearchChange = (value: string) => {
    setDebouncedSearch(value);
  };

  const handleViewModeChange = (mode: "list" | "grid") => {
    setViewMode(mode);
  };

  const handleFilterChange = (filters: any) => {
    setActiveFilters(filters);
    setShowFilters(
      Object.keys(filters).some(
        (key) =>
          filters[key] !== null &&
          (typeof filters[key] !== "object" ||
            (Array.isArray(filters[key]) &&
              (filters[key][0] > 0 || filters[key][1] < 500)))
      )
    );
  };

  const handlePageChange = (page: number) => {
    setSearchParams((prev) => {
      const newParams = new URLSearchParams(prev);
      newParams.set("page", page.toString());
      return newParams;
    });
  };

  // Apply client-side filters to the fetched data
  const filteredData = lists.filter((item) => {
    // Type filter
    const matchesType = activeFilters.type
      ? item.type === activeFilters.type
      : true;

    // Contacts range filter
    const matchesContactsRange = activeFilters.contactsRange
      ? item.contactCount >= activeFilters.contactsRange[0] &&
        item.contactCount <= activeFilters.contactsRange[1]
      : true;

    return matchesType && matchesContactsRange;
  });

  // Function to get list type (now directly from data)
  const getListType = (type: string) => {
    return type || "General";
  };

  // Function to get badge variant based on list type
  const getListBadgeVariant = (listType: string) => {
    switch (listType) {
      case "Marketing":
        return "purple";
      case "Event":
        return "info";
      case "Sales":
        return "success";
      case "Customer":
        return "warning";
      default:
        return "secondary";
    }
  };

  // Handle delete list - open confirmation modal
  const handleDeleteList = (item: any) => {
    setListToDelete(item);
    setDeleteDialogOpen(true);
  };

  // Confirm delete list
  const confirmDeleteList = async () => {
    if (!listToDelete) return;

    try {
      await deleteContactList(listToDelete.id.toString());
      fetchLists(); // Refresh the list
      toast.success("List disabled successfully");
    } catch (error) {
      console.error("Error deleting list:", error);
      toast.error("Failed to disable list");
    } finally {
      setDeleteDialogOpen(false);
      setListToDelete(null);
    }
  };

  // Handle restore list - open confirmation modal (super admin only)
  const handleRestoreList = (item: any) => {
    setListToRestore(item);
    setRestoreDialogOpen(true);
  };

  // Confirm restore list
  const confirmRestoreList = async () => {
    if (!listToRestore) return;

    try {
      await restoreContactList(listToRestore.id.toString());
      fetchLists(); // Refresh the list
      toast.success("List restored successfully");
    } catch (error) {
      console.error("Error restoring list:", error);
      toast.error("Failed to restore list");
    } finally {
      setRestoreDialogOpen(false);
      setListToRestore(null);
    }
  };

  // Common actions for both list and grid views
  const listActions = [
    {
      label: "View Contacts",
      icon: <Users className="h-4 w-4" />,
      onClick: (item: any) => {
        // Don't allow clicking on disabled lists unless super admin
        if (item.is_deleted && !isSuperAdmin) {
          toast.error("This list has been disabled and is not accessible.");
          return;
        }
        navigate(`/lists/${item.id}`);
      },
    },
    {
      label: "Edit List",
      icon: <Edit className="h-4 w-4" />,
      onClick: (item: any) => {
        // Don't allow editing disabled lists unless super admin
        if (item.is_deleted && !isSuperAdmin) {
          toast.error("This list has been disabled and cannot be edited.");
          return;
        }
        console.log("Editing", item);
      },
      condition: (item: any) => !item.is_deleted || isSuperAdmin, // Only show for active lists or super admin
    },
    ...(isSuperAdmin ? [
      {
        label: "Restore List",
        icon: <RotateCcw className="h-4 w-4" />,
        onClick: handleRestoreList,
        condition: (item: any) => item.is_deleted, // Only show for deleted items
      }
    ] : []),
    {
      label: "Delete List",
      icon: <Trash className="h-4 w-4" />,
      onClick: handleDeleteList,
      condition: (item: any) => !item.is_deleted, // Only show for non-deleted items
    },
  ];

  return (
    <Page>
      <Header
        title="Contact Lists"
        buttonText="List"
        action={handleAddListClick}
        filterByName={true}
        filterWord={debouncedSearch}
        onFilterChange={handleSearchChange}
        useSheet={false}
        sheetContent={null}
        sheetSize="half"
        viewMode={viewMode}
        onViewModeChange={handleViewModeChange}
        showViewToggle={true}
        showFilters={false} // We'll use our custom filter component instead
      />

      <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between mb-4 gap-3">
        <div className="flex items-center space-x-2">
          <Badge variant="secondary" className="px-3 py-1">
            <ListChecks className="h-3 w-3 mr-1" />
            <span>{totalLists} Lists</span>
          </Badge>
          {loading && <span className="text-sm text-gray-500">Loading...</span>}
        </div>

        <ListFilterPopover onFilterChange={handleFilterChange} />
      </div>

      {showFilters && (
        <div className="mb-4">
          {/* Active filters will be displayed by the FilterPopover component */}
        </div>
      )}

      {viewMode === "list" ? (
        <ModernTable
          data={filteredData}
          isSelectable={true}
          showColumnSelection={true}
          onRowClick={handleListClick}
          columns={[
            {
              header: "List Name",
              accessor: "list_name",
              className: "min-w-[250px]",
              cell: (value, item) => {
                const isDisabled = item.is_deleted;
                return (
                  <div className={`flex items-center space-x-3 ${isDisabled ? 'opacity-50' : ''}`}>
                    <div className={`h-9 w-9 bg-gradient-to-br rounded-lg flex items-center justify-center ${
                      isDisabled
                        ? 'from-gray-200 to-gray-100'
                        : 'from-primary/20 to-primary/10'
                    }`}>
                      <ListFilter className={`h-5 w-5 ${isDisabled ? 'text-gray-400' : 'text-primary'}`} />
                    </div>
                    <div>
                      <div className={`font-medium ${isDisabled ? 'text-gray-500' : 'text-gray-900'}`}>
                        {String(value)}
                        {isDisabled && (
                          <span className="ml-2 text-xs bg-red-100 text-red-600 px-2 py-1 rounded-full">
                            Disabled
                          </span>
                        )}
                      </div>
                      <div className="text-xs text-gray-500">
                        ID: LIST-{String(item.id).padStart(4, "0")}
                      </div>
                    </div>
                  </div>
                );
              },
              sortable: true,
            },
            {
              header: "Type",
              accessor: "type",
              className: "min-w-[150px]",
              cell: (value) => {
                const listType = getListType(String(value));

                return (
                  <Badge
                    variant={getListBadgeVariant(listType)}
                    className="capitalize"
                  >
                    {listType}
                  </Badge>
                );
              },
              sortable: true,
            },
            {
              header: "Contacts",
              accessor: "contactCount",
              className: "min-w-[150px]",
              cell: (value) => (
                <div className="flex items-center space-x-2 ">
                  <Users className="h-4 w-4 text-gray-400" />
                  <span className="font-medium text-gray-900">
                    {Number(value).toLocaleString()}
                  </span>
                </div>
              ),
              sortable: true,
            },
            {
              header: "Created",
              accessor: "createdAt",
              className: "min-w-[150px]",
              cell: (value) => (
                <div className="flex items-center space-x-2">
                  <Calendar className="h-4 w-4 text-gray-400" />
                  <span className="text-gray-700">
                    {formatDate(String(value))}
                  </span>
                </div>
              ),
              sortable: true,
            },
          ]}
          actions={listActions}
          emptyState={
            <div className="text-center p-8">
              <div className="h-16 w-16 bg-primary/10 rounded-full flex items-center justify-center mx-auto mb-4">
                <UserPlus className="h-8 w-8 text-primary" />
              </div>
              <h3 className="text-lg font-medium mb-1">
                {loading ? "Loading lists..." : "No contact lists found"}
              </h3>
              <p className="text-gray-500 mb-4">
                {loading ? "Please wait while we fetch your lists" : "Get started by creating your first contact list"}
              </p>
              
            </div>
          }
        />
      ) : (
        <ListGrid
          data={filteredData}
          onItemClick={handleListClick}
          actions={listActions}
          getListType={getListType}
          getListBadgeVariant={getListBadgeVariant}
          formatDate={formatDate}
        />
      )}

      {/* <div className="mt-8 border-t border-gray-200 pt-4">
        <CustomPagination
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={handlePageChange}
        />
      </div> */}

      {/* Delete Confirmation Dialog */}
      <AlertDialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Disable List</AlertDialogTitle>
            <AlertDialogDescription>
              Are you sure you want to disable this list? It will be hidden from regular users but can be restored by super admins.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction onClick={confirmDeleteList}>
              OK
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Restore Confirmation Dialog */}
      <AlertDialog open={restoreDialogOpen} onOpenChange={setRestoreDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Restore List</AlertDialogTitle>
            <AlertDialogDescription>
              Are you sure you want to restore this list? It will become visible and accessible to all users again.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction onClick={confirmRestoreList}>
              OK
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Insufficient Credits Modal */}
      <Dialog open={showInsufficientCreditsModal} onOpenChange={setShowInsufficientCreditsModal}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <div className="w-8 h-8 rounded-full bg-red-100 flex items-center justify-center">
                <AlertCircle className="w-4 h-4 text-red-600" />
              </div>
              <div>
                <h3 className="text-lg font-semibold">Insufficient Credits</h3>
                <p className="text-sm text-gray-500 font-normal">You need credits to create contact lists</p>
              </div>
            </DialogTitle>
          </DialogHeader>
          <div className="space-y-4">
            <div className="bg-gray-50 p-4 rounded-lg">
              <div className="flex justify-between items-center">
                <span className="text-sm text-gray-600">Current Balance:</span>
                <span className="font-semibold">{currentBalance} credits</span>
              </div>
              <div className="flex justify-between items-center mt-2">
                <span className="text-sm text-gray-600">Required:</span>
                <span className="font-semibold text-red-600">1 credit minimum</span>
              </div>
            </div>
            <div className="flex gap-3">
              <Button
                variant="outline"
                onClick={() => setShowInsufficientCreditsModal(false)}
                className="flex-1"
              >
                Cancel
              </Button>
              <Button
                onClick={() => {
                  setShowInsufficientCreditsModal(false);
                  window.location.href = '/credits';
                }}
                className="flex-1 bg-primary hover:bg-primary/90"
              >
                Buy Credits
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>

      {/* Add Contact List Sheet */}
      <Sheet open={showAddListSheet} onOpenChange={setShowAddListSheet}>
        <SheetContent size="half">
          <AddContactList />
        </SheetContent>
      </Sheet>
    </Page>
  );
};

export default Lists;
